import './App.css';
import { MyHand } from './MyHand.js';
import { CardTable } from './CardTable.js';
import { useState, useEffect } from 'react';

function App() {

  const [player,setPlayer] = useState()
  const [hand,setHand] = useState([])
  const [ws,setWs] = useState()
  const [game,setGame] = useState({
    "hearts": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "clubs": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "diamonds": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "spades": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ]
  })

  function getWs() {
    var HOST = "ws://jbsevens.herokuapp.com"
    return(new WebSocket(HOST));
  }

  useEffect( () => {

    let localws = getWs()

    setWs(localws);

    localws.onmessage = (event) => {
      console.log(event.data)
      let m = JSON.parse(event.data);
      switch(m.type) {
        case 'card':
          updateGame(m.card);
          break;
        case 'hand':
          updateHand(m.cards);
          break;
      }
    }

    localws.onopen = (event) => {
      sendHandRequest(localws, player)
    }
  },[player]);

  function updateHand(newhand) {
    console.log(`Update hand called with ${newhand}`)

    setHand(newhand)
  }

  function updateGame(card) {
    setGame( g => {

      let suit = card.length === 2 ? card.charAt(1) : card.charAt(2);
      let cardValue = card.length === 2 ? card.substring(0,1) : card.substring(0,2)
  
      const newTable = {
        hearts: g["hearts"].map( oldcard => {
          if (suit === "H" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        clubs: g["clubs"].map( oldcard => {
          if (suit === "C" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        diamonds: g["diamonds"].map( oldcard => {
          if (suit === "D" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        spades: g["spades"].map( oldcard => {
          if (suit === "S" && cardValue === oldcard.value) return { value: oldcard.value, played: true}
          else return { value: oldcard.value, played: oldcard.played}
        })
      }
    
      return(newTable)    
    })
  }

  function sendHandRequest(lws, p) {
    lws.send(JSON.stringify({
      type: 'hand',
      player: p
    }))

  }

  function sendCard(card) {
    ws.send(JSON.stringify({
      type: 'card',
      card: card
    }))
  }

  function updatePlayer(event) {
    setPlayer(event.target.value)
  }

  return (
    <div className="App">
      <div className="app-playerselecter">
      <label for="players">Choose a player:</label>
        <select name="players" id="players" onChange={updatePlayer}>
          <option value="...">...</option>
          <option value="Nancy">Nancy</option>
          <option value="John">John</option>
          <option value="Allen">Allen</option>
          <option value="Gavin">Gavin</option>
        </select>
      </div>
      <MyHand hand={hand} playCard={sendCard} />
      <CardTable game={game} />
    </div>
  );
}

export default App;
